@import url(https://fonts.googleapis.com/css2?family=PT+Sans&family=Roboto+Condensed&display=swap);
:root {
  --primary: #232F3E;
  --secondary: #F3A847;
  --tertiary: #B12704;
  --light: #fafafa;
  --dark: #272f32;
  --error: rgb(228, 46, 1);
  --spacing-one: calc(1rem * 0.5);
  --spacing-two: calc(1rem);
}

* {
  box-sizing: border-box;
}

html,
body {
  min-height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Karla', sans-serif;
  font-weight: 400;
  line-height: 1.5;
  font-size: 16px;
  color: #272f32;
  color: var(--dark);
  background-color: #82709c;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Rubik', sans-serif;
  font-weight: 500;
  margin: 5px 0;
  line-height: 1.25;
  color:#fff
}

h1 {
  font-size: 4.25rem;
 
}
.text {
  font-family: "cursive";
  font-style: italic;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.75rem;
}

h4 {
  font-size: 1.5rem;
}

h5 {
  font-size: 1.25rem;
}

h6 {
  font-size: 1rem;
}

p {
  font-size: 1.1rem;
  margin-top: 0;
  color:#fff
}

a {
  color: #212121;
  text-decoration: none;
  font-weight: bold;
  background-color: #82709c;
  border-radius: 25px;
  font-size: 75px;
}

.centering {
  margin-top: 3rem;
  margin-bottom: 8rem;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

/* NAVIGATION */
header {
  background-image: url(/static/media/background.0418af75.jpg);
  justify-content: space-between;
  padding: 10px;
  height: 15vh;

  box-shadow: 15px 15px 15px #000;

}

header h1 {
  margin-top: auto;
  margin-bottom: auto;
  margin-left: 0.75rem;
  font-family: 'PT Sans', Helvetica, Arial, sans-serif;
  letter-spacing: .2rem;
}

header a {
  color: #fff;
  font-size: 20px;
}

nav {
  position: relative;
  margin-right: 4%;
  margin-top: auto;
  margin-bottom: auto;
  font-family: 'PT Sans', Helvetica, Arial, sans-serif;
  justify-content: space-around;
  font-size: medium;
  padding: 0 5px;
}

.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #212121;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  padding: 12px 16px;
  z-index: 1;
}

.dropdown:hover .dropdown-content {
  display: block;
}

.ulmove {
  padding-left: 0;
}

.linone {
  list-style-type: none;
  list-style-position: outside;
}

.namesize {
  font-size: 1.25rem;
}

button {
  font-size: 1rem;
  text-decoration: none;
  padding: 0.25rem 1rem;
  border-radius: .5rem;
  border: none;
  outline: none;
  cursor: pointer;
  margin: 0 1px;
  background-color: rgba(237, 53, 151, 0.29);
  color:#fff;

}

.buttonsizing {
  width: 8rem;
  height: 5rem;
  border: #212121 solid 1px;
}

button:hover {
  opacity: .8;
}

button[disabled] {
  cursor: default;
  opacity: .8;
}

/* PRODUCTS LIST */
img {
  max-width: 100%;
  max-height: 100%;
  width: 393px;
  height: 300px;
  object-fit: cover;
  box-shadow: 15px 15px 15px #000;
  border-radius: 25px;
}

span[role="img"] {
  cursor: default;
}
div {
  color:#fff;
}

form {
  width: 40%;
}

input {
  font-size: 1rem;
  padding: 0.25rem;
  border: 1px solid #272f32;
  border: 1px solid var(--dark);
  outline: none;
  border-radius: 3px;
  transition: box-shadow 0.2s;
}

input:focus {
  border: 3px solid #F3A847;
  border: 3px solid var(--secondary);
  box-shadow: 0 0 .25rem rgba(0, 0, 0, .5);
}

.container {
  width: 85%;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

.flex-row {
  display: flex;
  flex-wrap: wrap;
}

.space-between {
  justify-content: space-between;
}

.flex-end {
  justify-content: flex-end;
}

.mx-1 {
  margin-right: calc(1rem * 0.5);
  margin-right: var(--spacing-one);
  margin-left: calc(1rem * 0.5);
  margin-left: var(--spacing-one);
}

.mx-2 {
  margin-right: calc(1rem);
  margin-right: var(--spacing-two);
  margin-left: calc(1rem);
  margin-left: var(--spacing-two);
}

.my-1 {
  margin-top: calc(1rem * 0.5);
  margin-top: var(--spacing-one);
  margin-bottom: calc(1rem * 0.5);
  margin-bottom: var(--spacing-one);
}

.my-2 {
  margin-top: calc(1rem);
  margin-top: var(--spacing-two);
  margin-bottom: calc(1rem);
  margin-bottom: var(--spacing-two);
}

.px-1 {
  padding-right: 0px;
  padding-left: 0px;
}

.px-2 {
  padding-right: calc(1rem);
  padding-right: var(--spacing-two);
  padding-left: calc(1rem);
  padding-left: var(--spacing-two);
}

.py-1 {
  padding-top: calc(1rem * 0.5);
  padding-top: var(--spacing-one);
  padding-bottom: calc(1rem * 0.5);
  padding-bottom: var(--spacing-one);
}

.py-2 {
  padding-top: calc(1rem);
  padding-top: var(--spacing-two);
  padding-bottom: calc(1rem);
  padding-bottom: var(--spacing-two);
}

.card {
  width: 26%;
  text-align: center;
  margin-left: auto;
  margin-bottom: 2rem;
  
}


.error-text {
  color: rgb(228, 46, 1);
  color: var(--error);
}

@media screen and (max-width: 768px) {
  .card {
    width: 100%;
  }
}
.cart {
  position: fixed;
  top: 0;
  right: 0;
  min-width: 50%;
  max-width: 50%;
  max-height: 50%;
  background-color: var(rgb(179, 132, 132));
  overflow: auto;
  padding: .5rem;
  box-shadow: 0 0 1rem rgba(0, 0, 0, .5);
  /* border-bottom-left-radius: .5rem; */
  background-color: rgb(106, 87, 199);
  margin: auto;
}

.cart h2 {
  font-size: 1.5rem;
  border-bottom: 1px solid var(--dark);
  padding-bottom: .5rem;
  margin: 1rem 0;
}

.cart .close {
  position: absolute;
  top: .5rem;
  right: .5rem;
  cursor: pointer;
  color: var(--tertiary);
}

.cart .close:hover {
  text-decoration: underline;
}

.cart-closed {
  position: fixed;
  top: 2%;
  right: 1%;
  font-size: 2rem;
  cursor: pointer;
  background-color: white;
  border-radius: 50%;
  padding: .25rem;
  width: 50px;
  height: 50px;
  margin-top: 5rem;
}

.cart-closed:hover {
  -webkit-transform: rotate(8deg);
          transform: rotate(8deg);
}

.cart img {
  width: 70px;
  margin-right: 1rem;
}

.cart input {
  width: 50px;
  padding: 0;
  margin: 0 .5rem 1rem 0;
}

@media screen and (max-width: 768px) {
  .cart {
    position: relative;
    max-width: 100%;
    max-height: auto;
    box-shadow: none;
    background-color: inherit;
  }

  .cart-closed {
    position: relative;
    margin: 0 auto;
  }
}
